import React, { useState } from 'react'

const Progress = ({ done }) => {
  const [style, setStyle] = useState({})

  setTimeout(() => {
    const newStyle = {
      opacity: 1,
      width: `${done}%`
    }

    setStyle(newStyle)
  }, 80)

  //{done}%
  return (
    <div className="progress">
      <div className="progress-done" style={style}>
      </div>
    </div>
  )
}

export default Progress